import moment from 'moment'
import { useState, useEffect } from 'react'
import firebase from '../firebase'
import 'moment/locale/pt-br';
import { format } from 'date-fns';

export function useTodos(){
    const [todos, setTodos] = useState([])
    

    useEffect(() => {
        let unsubscribe = firebase
        .firestore()
        .collection('todos')
        .onSnapshot( snapshot => {
            const data = snapshot.docs.map( doc => {
                return {
                    id : doc.id,
                    ...doc.data()
                }
            })
            setTodos(data)
        })

        return () => unsubscribe()
    }, [])

    return todos
}

export function useFilterTodos(todos, selectedProject){
    const [filteredTodos, setFilteredTodos] = useState([])
    moment.locale('pt-br');

    useEffect( () => {
        let data;
        const todayDateFormated = moment().format('DD/MM/YYYY')
        if(selectedProject === 'Hoje'){
            data = todos.filter(todo => todo.date == todayDateFormated)
        }else if(selectedProject === 'Pr칩ximos 7 dias'){
            data = todos.filter(todo => {
                const todoDate = moment(todo.date, 'DD/MM/YYYY')
                const todayDate = moment(todayDateFormated, 'DD/MM/YYYY')

                const diffDays = todoDate.diff(todayDate, 'days')

                return diffDays >= 0 && diffDays < 7
            })
        }else if( selectedProject === 'Todos os dias'){
            data = todos
        }else{
            data = todos.filter(todo => todo.projectName === selectedProject)
        }

        setFilteredTodos(data)
    }, [todos, selectedProject])

    return filteredTodos
}

export function useProjects(){
    const [projects, setProjects] = useState([])


    useEffect(() => {
        let unsubscribe = firebase
        .firestore()
        .collection('projects')
        .onSnapshot( snapshot => {
            const data = snapshot.docs.map( doc => {

                return {
                    id : doc.id,
                    name : doc.data().name
                }
            })
            setProjects(data)
        })

        return() => unsubscribe()
    }, [])

    return projects
}

export function useProjectWithStats(projects, todos) {
    const [projectWithStats, setProjectWithStats] = useState([])

    useEffect( () => {
        const data = projects.map((project) => {
            return {
                numOfTodos : todos.filter( todo => todo.projectName === project.name && !todo.checked).length,
                ...project
            }
        })

        setProjectWithStats(data)
    }, [projects, todos])

    return projectWithStats
}
